import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { FullMockTestDetailResolver } from "@app/modules/other-evaluate/reslovers/full-mocktest-detail.reslover";
import { OtherEvaluateReslover } from "@app/modules/other-evaluate/reslovers/other-evaluate.reslover";
import { FullMocktestEvaluateComponent } from "@app/modules/other-evaluate/full-mocktest-evaluate/full-mocktest-evaluate.component";
import { PredictionEvaluateComponent } from "@app/modules/other-evaluate/prediction-evaluate/prediction-evaluate.component";


const routes: Routes = [
  {
    path: ':moduleName/:evaluateId/enter-study-plan',
    component: PredictionEvaluateComponent,
    resolve: {
      questionList: OtherEvaluateReslover,
    },
    data: { evaluateName: 'study-plan' }
  },
  {
    path: ':moduleName/:evaluateId/enter-prediction',
    component: PredictionEvaluateComponent,
    resolve: {
      questionList: OtherEvaluateReslover,
    },
    data: { evaluateName: 'prediction' }
  },
  {
    path: ':moduleName/:evaluateId/enter-mock-test',
    component: PredictionEvaluateComponent,
    resolve: {
      questionList: OtherEvaluateReslover,
    },
    data: { evaluateName: 'mock-test' }
  },
  {
    path: 'evaluate-full-mock-test/:mockId',
    component: FullMocktestEvaluateComponent,
    resolve: {
      questionList: FullMockTestDetailResolver,
    }
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
  providers: [
    OtherEvaluateReslover,
    FullMockTestDetailResolver
  ]
})

export class OtherEvaluateRoutingModule { }
