import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { EvaluateQuestionService } from '@app/core/http/evaluate-question.service';
import { SaveService } from '@app/core/http/save.service';
import { FullMockEvaluate } from '@app/shared/models/mock-test-list';
import { QuestionByModuleComponent } from '@app/shared/question-components/question-by-module/question-by-module.component';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-full-mocktest-evaluate',
  templateUrl: './full-mocktest-evaluate.component.html',
  styleUrls: ['./full-mocktest-evaluate.component.css']
})
export class FullMocktestEvaluateComponent implements OnInit {
  @ViewChild(QuestionByModuleComponent, { static: false }) private questionModule: QuestionByModuleComponent;
  
  public mockQuestions: any;

  public moduleIndex: number = 0;
  public selectedIndex: number = 0;
  public currentQuestions: any = [];

  private mockEvaluate = <FullMockEvaluate>{
    userAnswers: [],
    files: []
  };
  private subscription: Subscription;
  public scoreData:any;

  constructor(private _activeRoute: ActivatedRoute, private _saveService: EvaluateQuestionService) { }

  ngOnInit() {
    this.mockQuestions = this._activeRoute.snapshot.data['questionList'].questions;
    this.currentQuestions = this.mockQuestions[this.moduleIndex];
    this.mockEvaluate.mockTestId = this._activeRoute.snapshot.params['mockId'];
  }


  getAnswer(ans) {
    this.mockEvaluate.userAnswers.push(ans);
    if(this.isTimerEnd==true){
      if (this.moduleIndex < this.mockQuestions.length - 1) {
        this.moduleIndex += 1;
        this.currentQuestions = this.mockQuestions[this.moduleIndex];
        this.selectedIndex = 0;
        this.isTimerEnd=false;
      }
      else this.onSubmit();
    }
    else{
      if (this.selectedIndex < this.currentQuestions.questions.length - 1) {
        this.selectedIndex += 1;
      } else {
        if (this.moduleIndex < this.mockQuestions.length - 1) {
          this.moduleIndex += 1;
          this.currentQuestions = this.mockQuestions[this.moduleIndex];
          this.selectedIndex = 0;
        }
        else this.onSubmit();
      }
    }
    
  }

  private isTimerEnd:boolean=false;

  onTimeEnd(e) {
    if(e.action=='done'){
      this.isTimerEnd=true;
      this.questionModule.onSubmit();
    }

  }

  onSubmit() {
    this.subscription = this._saveService.onEvaluateFullMock(this.mockEvaluate)
      .subscribe((res) => {
        this.scoreData=res
      })
  }

  ngOnDestroy(): void {
    //Called once, before the instance is destroyed.
    //Add 'implements OnDestroy' to the class.
    if (this.subscription) this.subscription.unsubscribe();
  }

  ngAfterViewInit(): void {
    //Called after ngAfterContentInit when the component's view has been initialized. Applies to components only.
    //Add 'implements AfterViewInit' to the class.
    
  }
}
