import { ActivatedRouteSnapshot, Resolve } from '@angular/router';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';;

import { DetailService } from '@app/core/http/detail.service';


@Injectable()
export class FullMockTestDetailResolver implements Resolve<any> {

    constructor(private service: DetailService) {
    }

    resolve(route: ActivatedRouteSnapshot):
        Observable<any> {
            let id=route.params['mockId'].toLowerCase();

            return this.service.getDetail(`mock-test/${id}/enter-mock-test`);
    }
}